import React from "react";

const CardProducto = ({
  img,
  titulo,
  fecha,
  descripcion,
  claseBoton,
  textoBoton,
  estadoModal1,
  CambiarEstadoModal1,
  clase_h1,
  clase_span,
}) => {
  return (
    <>
      <div className="card ">
        <img src={img} className="card__img" alt="..." />
        <div className="card__data">
          <h1 className={clase_h1}>{titulo}</h1>
          <span className={clase_span}>{fecha}</span>
          <p className="card__description">{descripcion}</p>
          <button
            className={claseBoton}
            type="button"
            onClick={() => {
              CambiarEstadoModal1(!estadoModal1);
            }}
          >
            {textoBoton}
          </button>
        </div>
      </div>
    </>
  );
};

export default CardProducto;
