import React from "react";

const RequisitosGenerales = () => {
  return (
    <>
      <div className="container">
        <div className="row">
          <div className="d-flex justify-content-center">
            <h1 className="subtitulo_contenedor">
              Requisitos Generales Para Estudios de Posgrado
            </h1>
          </div>
          <div className="d-flex justify-content-center">
            <table className="table table-striped">
              <thead></thead>
              <tbody>
                <tr>
                  <td>Ficha de datos</td>
                </tr>
                <tr>
                  <td>
                    Solicitud dirigida al Director de la Escuela Central de
                    Posgrado
                  </td>
                </tr>
                <tr>
                  <td>
                    Declaración jurada, obligandose a cumplir el Estatuto de la
                    UNI y el Reglamento vigente
                  </td>
                </tr>
                <tr>
                  <td>
                    Cartas de presentación de dos profesores reconocidos, de
                    preferencia de su universidad de origen, según formato
                  </td>
                </tr>
                <tr>
                  <td>Curriculum vitae documentado, copias simples</td>
                </tr>
                <tr>
                  <td>
                    Copia autenticada: del grado de bachiller (para maestría),
                    del grado de maestro (para doctorado)
                  </td>
                </tr>
                <tr>
                  <td>
                    Copia autenticada: del certificado universitario (para
                    maestría), del certificado de maestría (para doctorado)
                  </td>
                </tr>
                <tr>
                  <td>Copia simple del DNI</td>
                </tr>
                <tr>
                  <td>
                    Cuatro fotos recientes a color, tamaño carné, fondo blanco y
                    sin lentes
                  </td>
                </tr>
                <tr>
                  <td>Recibos de pago por derecho de carpeta y admisión</td>
                </tr>
                <tr>
                  <td>
                    Postulantes por modalidad de pre-maestría: constancia
                    original que acredite su aprobación
                  </td>
                </tr>
                <tr>
                  <td>
                    Postulantes por modalidad de traslado: copia legalizada del
                    certificado de estudios de maestría
                  </td>
                </tr>
                <tr>
                  <td>
                    De acuerdo a las modalidades de admisión, el postulante
                    deberá cumplir con los requisitos complementarios
                    establecidos en el reglamento de admisión a Estudios de
                    Posgrado
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
};

export default RequisitosGenerales;
