import { URL_BACKEND } from "./../environments/environments";
import axios from "axios";

export const getHeader = async () => {
  const rpta = await axios.get(URL_BACKEND + "/header");
  return rpta;
};

export const getFechaModalidad = async () => {
  const rpta = await axios.get(URL_BACKEND + "/fechaModalidad");
  return rpta;
};
