import React, { useContext } from "react";
import Typewriter from "typewriter-effect";
import { FaInstagram } from "react-icons/fa";
import { FaTwitter } from "react-icons/fa";
import { FaGooglePlusG } from "react-icons/fa";
import { FaFacebookF } from "react-icons/fa";
import { FaGlobeAmericas } from "react-icons/fa";
import PreMaestriaContext from "../context/PreMaestriaContext";
const Header = () => {
  const { header } = useContext(PreMaestriaContext);
  // console.log(header[0]);
  return (
    <div className="text-header d-flex">
      <div className="container">
        <div className="row justify-content-center align-items-center row__header">
          <div className="col-sm-8 ">
            <h1 className="titulo__h1">
              {header[0] ? header[0].periodo : "..."} :
              <Typewriter
                options={{
                  strings: [
                    header[0] ? header[0].fechaPreMaestria : "...",
                    header[0] ? header[0].fechaExamen : "...",
                  ],
                  autoStart: true,
                  loop: true,
                }}
              />
            </h1>
            <p>UNI, Facultad De Ingeniería Mecánica Unidad De Posgrado</p>

            <ul className="list-unstyled">
              <li>
                <a
                  href="https://www.facebook.com/PosgradoFacultadIngenieriaMecanica.UNI"
                  target="”_blank”"
                >
                  <FaFacebookF />
                </a>
              </li>
              <li>
                <a href="!#">
                  <FaTwitter />
                </a>
              </li>
              <li>
                <a href="!#">
                  <FaInstagram />
                </a>
              </li>

              <li>
                <a href="posgrado_fim@uni.edu.pe" target="”_blank”">
                  <FaGooglePlusG />
                </a>
              </li>
              <li>
                <a
                  href="https://upfim.uni.edu.pe/unidad_posgrado/"
                  target="”_blank”"
                >
                  <FaGlobeAmericas />
                </a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
