import React from "react";

const Tarifas = () => {
  return (
    <>
      <div className="container">
        <div className="row">
          <div className="d-flex justify-content-center">
            <h1 className="subtitulo_contenedor">Tarifas de Pago</h1>
          </div>
          <div>
            <table className="table table-striped">
              <thead>
                <tr>
                  <th scope="col">DESCRIPCIÓN</th>
                  <th scope="col">MAESTRÍA</th>
                  <th scope="col">DOCTORADO</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <th scope="row">Prospecto de Admisión</th>
                  <td>S/. 250.00</td>
                  <td>S/. 250.00</td>
                </tr>
                <tr>
                  <th scope="row">Derecho de Admisión</th>
                  <td>S/. 875.00</td>
                  <td>S/. 875.00</td>
                </tr>
                <tr>
                  <th scope="row">Matrícula</th>
                  <td>S/. 656.00 x Ciclo</td>
                  <td>S/. 919.00 x Ciclo</td>
                </tr>
                <tr>
                  <th scope="row">Crédito</th>
                  <td>S/. 424.00</td>
                  <td>S/. 532.00</td>
                </tr>
                <tr>
                  <th scope="row">Certificado de finalización de Estudios</th>
                  <td>S/. 294.00</td>
                  <td>S/. 294.00</td>
                </tr>
                <tr>
                  <th scope="row">Constancia de No Adeudar a la UNI</th>
                  <td>S/. 66.00</td>
                  <td>S/. 66.00</td>
                </tr>
                <tr>
                  <th scope="row">Grados</th>
                  <td>S/. 4375.00</td>
                  <td>S/. 5250.00</td>
                </tr>
              </tbody>
            </table>
            <div>
              <ul class="list-group">
                <li class="list-group-item active">Nota:</li>
                <li class="list-group-item">
                  Los profesores nombrados y contratados a tiempo completo con
                  más de tres años de servicios continuos en la UNI, en
                  actividad, pagarán el 20% del Prospecto de Admisión y Derecho
                  de Admisión.
                </li>
                <li class="list-group-item">
                  15% de descuento en los créditos, Prospecto de admisión y
                  Derecho de Admisión para los alumnos egresados de la UNI.
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Tarifas;
