import React, { useState } from "react";
import PreMaestriaContext from "./PreMaestriaContext";
import { getMaestrias } from "../services/maestriasService";
import { getFechaModalidad, getHeader } from "../services/bannerService";
import { useEffect } from "react";

const PreMaestriaState = (props) => {
  const [estadoModal1, CambiarEstadoModal1] = useState(false);
  const [estadoModal2, CambiarEstadoModal2] = useState(false);
  const [estadoModal3, CambiarEstadoModal3] = useState(false);
  const [maestrias, setMaestrias] = useState([]);
  const [header, setHeader] = useState([]);
  const [fechaModalidades, setfechaModalidades] = useState([]);
  useEffect(() => {
    getMaestrias().then((rpta) => {
      if (rpta.data) {
        setMaestrias(rpta.data);
      }
    });
  }, []);
  useEffect(() => {
    getHeader().then((rpta) => {
      if (rpta.data) {
        setHeader(rpta.data);
      }
    });
  }, []);
  useEffect(() => {
    getFechaModalidad().then((rpta) => {
      if (rpta.data) {
        setfechaModalidades(rpta.data);
      }
    });
  }, []);
  return (
    <PreMaestriaContext.Provider
      value={{
        fechaModalidades: fechaModalidades,
        header: header,
        maestrias: maestrias,
        estadoModal1: estadoModal1,
        CambiarEstadoModal1: CambiarEstadoModal1,
        estadoModal2: estadoModal2,
        CambiarEstadoModal2: CambiarEstadoModal2,
        estadoModal3: estadoModal3,
        CambiarEstadoModal3: CambiarEstadoModal3,
      }}
    >
      {props.children}
    </PreMaestriaContext.Provider>
  );
};

export default PreMaestriaState;
