import React from "react";
import styled from "styled-components";
import Modal from "./Modal";

const ModalPreMaestria = ({ estadoModal1, CambiarEstadoModal1, maestrias }) => {
  return (
    <>
      <Modal
        estado={estadoModal1}
        CambiarEstadoModal1={CambiarEstadoModal1}
        titulo="Modalidad de:"
        mostrarHeader={false}
        mostrarOverlay={true}
        posisionModal={"center"}
        padding={"20px"}
      >
        <Contenido>
          <h1>PRE MAESTRÍA</h1>
          <p>
            Dirigido a ingenieros, licenciados, economistas, bachilleres y
            egresados en ciencias e ingeniería, para aspirar al Ingreso Directo
            a las siguientes especialidades:
          </p>
          <ul className="list-group">
            {maestrias.map((objMaestrias) => {
              return (
                <li className="list-group-item" key={objMaestrias.id}>
                  {objMaestrias ? objMaestrias.nombre : "..."}
                </li>
              );
            })}
          </ul>

          <div className="d-flex flex-row subcontenidoModal">
            <div className="listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Materías</li>
                <li className="list-group-item">
                  Matemáticas aplicadas para ingenieros
                </li>
                <li className="list-group-item">
                  Metodología de la investigación
                </li>
                <li className="list-group-item">Gestión de proyectos</li>
              </ul>
            </div>
            <div className=" listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Horario de Clases</li>
                <li className="list-group-item">
                  Martes y jueves de 18:00 a 22:00 horas y sábados de 09:00 a
                  13:00 horas
                </li>
              </ul>
            </div>
            <div className=" listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Costo</li>
                <li className="list-group-item">
                  S/. 1,400.00 (Mil cuatrocientos con 00/100 soles)
                </li>
              </ul>
            </div>
            <div className="listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Modo de Pago</li>
                <li className="list-group-item">
                  Solicitar orden de pago BCP al correo posgrado_fim@uni.edu.pe,
                  brindando los siguientes datos: Nombres y Apellidos, DNI y
                  número de celular y en el caso de solicitar factura indicar
                  razón social, RUC y dirección
                </li>
              </ul>
            </div>
          </div>
        </Contenido>
      </Modal>
    </>
  );
};

export default ModalPreMaestria;

const Contenido = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 42px;
    font-weight: 700;
    margin-bottom: 30px;
    margin-top: 30px;
  }

  p {
    font-size: 18px;
    margin-bottom: 20px;
  }

  img {
    width: 100%;
    vertical-align: top;
    border-radius: 3px;
  }
`;
