import React from "react";
import styled from "styled-components";
import Modal from "./Modal";

const ModalClasesMaestria = ({
  estadoModal3,
  CambiarEstadoModal3,
  maestrias,
}) => {
  return (
    <>
      <Modal
        estado={estadoModal3}
        CambiarEstadoModal1={CambiarEstadoModal3}
        titulo="Modalidad de:"
        mostrarHeader={false}
        mostrarOverlay={true}
        posisionModal={"center"}
        padding={"20px"}
      >
        <Contenido>
          <h1>INFORMES DE LAS MAESTRÍAS</h1>
          <p>
            La Unidad de Posgrado de la Facultad de Ingeniería Mecánica de la
            Universidad Nacional de Ingeniería, le invita a usted a participar
            al proceso de ADMISIÓN, de las siguientes maestrías:
          </p>
          <ul className="list-group">
            {maestrias.map((objMaestrias) => {
              return (
                <li className="list-group-item" key={objMaestrias.id}>
                  {objMaestrias ? objMaestrias.nombre : "..."}
                </li>
              );
            })}
          </ul>

          <div className=" d-flex flex-row subcontenidoModal">
            <div className="listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">
                  Duración de la Maestría
                </li>
                <li className="list-group-item">DURACIÓN : DOS AÑOS</li>
                <li className="list-group-item">CICLOS : 04 CICLOS</li>
                <li className="list-group-item">
                  DURACIÓN POR CICLO: 04 MESES
                </li>
              </ul>
            </div>
            <div className=" listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Horario de Clases</li>
                <li className="list-group-item">Sábados de 08:30 a 20:00</li>
              </ul>
            </div>
            <div className=" listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Inversión</li>
                <li className="list-group-item">
                  MATRÍCULA : S/. 656.00 (Seiscientos cincuenta y seis con
                  00/100 soles) Este pago se realiza cada ciclo
                </li>
                <li className="list-group-item">
                  CREDITO : S/. 424.00 (Cuatrocientos veinte y cuatro con 00/100
                  soles) Costo por cada Crédito
                </li>
              </ul>
            </div>
            <div className=" listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Modo de Pago</li>
                <li className="list-group-item">
                  Deposito – Banco Scotiabank: Indicar en ventanilla
                  Autogenerado UNI – FIM Concepto: Código 335 CREDITOS , Código
                  334 MATRICULA (DE SOLICITAR FACTURA INDICAR RAZON SOCIAL Y
                  RUC)
                </li>
                <li className="list-group-item">
                  Solicitar orden de pago BCP al correo posgrado_fim@uni.edu.pe,
                  brindando los siguientes datos: Nombres y Apellidos, DNI y
                  número de celular y en el caso de solicitar factura indicar
                  razón social, RUC y dirección
                </li>
              </ul>
            </div>
          </div>
        </Contenido>
      </Modal>
    </>
  );
};

export default ModalClasesMaestria;

const Contenido = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 42px;
    font-weight: 700;
    margin-bottom: 30px;
    margin-top: 30px;
  }

  p {
    font-size: 18px;
    margin-bottom: 20px;
  }

  img {
    width: 100%;
    vertical-align: top;
    border-radius: 3px;
  }
`;
