import React, { useContext } from "react";
import CardProducto from "../componentes/CardProducto";
import img from "../img/examen.png";
import libros from "../img/libros.png";
import graduado from "../img/graduado.png";
import ModalPreMaestria from "./ModalPreMaestria";
import ModalExamenAdminsio from "./ModalExamenAdminsio";
import PreMaestriaContext from "../context/PreMaestriaContext";
import ModalClasesMaestria from "./ModalClasesMaestria";

const Modalidades = () => {
  const {
    fechaModalidades,
    maestrias,
    estadoModal1,
    CambiarEstadoModal1,
    estadoModal2,
    CambiarEstadoModal2,
    estadoModal3,
    CambiarEstadoModal3,
  } = useContext(PreMaestriaContext);
  return (
    <>
      <div className="container">
        <div className="row d-flex justify-content-center row__conteniedo">
          <div className="d-flex justify-content-center">
            <h1 className="subtitulo_contenedor">Modalidad</h1>
          </div>
          <CardProducto
            img={libros}
            clase_h1="card__title_01"
            clase_span="card__preci_01"
            titulo="Pre Maestría"
            descripcion="Dirigido a ingenieros, licenciados, economistas, bachilleres y egresados en ciencias e ingeniería, para aspirar al Ingreso Directo."
            fecha={fechaModalidades[0] ? fechaModalidades[0].fechaPre : "..."}
            claseBoton="card__button"
            textoBoton="Ver Detalles"
            estadoModal1={estadoModal1}
            CambiarEstadoModal1={CambiarEstadoModal1}
          />
          <ModalPreMaestria
            maestrias={maestrias}
            estadoModal1={estadoModal1}
            CambiarEstadoModal1={CambiarEstadoModal1}
          />
          <CardProducto
            img={img}
            clase_h1="card__title_02"
            clase_span="card__preci_02"
            titulo="Examen de Admisión"
            descripcion="La Unidad de Posgrado de la Facultad de Ingeniería Mecánica de la Universidad Nacional de Ingeniería, le invita a usted a participar al proceso de ADMISIÓN."
            fecha={
              fechaModalidades[0] ? fechaModalidades[0].fechasExamen : "..."
            }
            claseBoton="card__button"
            textoBoton="Ver Detalles"
            estadoModal1={estadoModal2}
            CambiarEstadoModal1={CambiarEstadoModal2}
          />
          <ModalExamenAdminsio
            maestrias={maestrias}
            estadoModal2={estadoModal2}
            CambiarEstadoModal2={CambiarEstadoModal2}
          />
          <CardProducto
            img={graduado}
            clase_h1="card__title_03"
            clase_span="card__preci_03"
            titulo="Inicio de la Maestría"
            descripcion="La Unidad de Posgrado de la Facultad de Ingeniería Mecánica de la Universidad Nacional de Ingeniería, le invita a usted a ser parte de nuestro selecto grupo de alumnos."
            fecha={
              fechaModalidades[0] ? fechaModalidades[0].fechaClases : "..."
            }
            claseBoton="card__button"
            textoBoton="Ver Detalles"
            estadoModal1={estadoModal3}
            CambiarEstadoModal1={CambiarEstadoModal3}
          />
          <ModalClasesMaestria
            maestrias={maestrias}
            estadoModal3={estadoModal3}
            CambiarEstadoModal3={CambiarEstadoModal3}
          />
        </div>
      </div>
    </>
  );
};

export default Modalidades;
