import React from "react";
import styled from "styled-components";
import Modal from "./Modal";

const ModalExamenAdminsio = ({
  estadoModal2,
  CambiarEstadoModal2,
  maestrias,
}) => {
  return (
    <>
      <Modal
        estado={estadoModal2}
        CambiarEstadoModal1={CambiarEstadoModal2}
        titulo="Modalidad de:"
        mostrarHeader={false}
        mostrarOverlay={true}
        posisionModal={"center"}
        padding={"20px"}
      >
        <Contenido>
          <h1>EXAMEN DE ADMISIÓN</h1>
          <p>
            La Unidad de Posgrado de la Facultad de Ingeniería Mecánica de la
            Universidad Nacional de Ingeniería, le invita a usted a participar
            al proceso de ADMISIÓN, de las siguientes maestrías:
          </p>
          <ul className="list-group">
            {maestrias.map((objMaestrias) => {
              return (
                <li className="list-group-item" key={objMaestrias.id}>
                  {objMaestrias ? objMaestrias.nombre : "..."}
                </li>
              );
            })}
          </ul>
          <br />
          <p>Cada programa se inicia con mínimo de 10 alumnos</p>
          <div className=" d-flex justify-content-center subcontenidoModal">
            <div className="listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Requisitos</li>
                <li className="list-group-item">
                  Copia autenticada del grado académico de bachiller para
                  maestría
                </li>
                <li className="list-group-item">Currículo vitae documentado</li>
                <li className="list-group-item">
                  Copia autenticada del certificado de estudios universitarios
                </li>
                <li className="list-group-item">Copia simple de DNI</li>
                <li className="list-group-item">
                  Cuatro fotografías recientes a color, tamaño carné, fondo
                  blanco y sin lentes
                </li>
                <li className="list-group-item">
                  Recibos de pago por prospecto y derecho de admisión
                </li>
              </ul>
            </div>
            <div className=" listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Costo</li>
                <li className="list-group-item">
                  PROSPECTO DE ADMISIÓN: S/. 250.00 (Doscientos cincuenta con
                  00/100 soles)
                </li>
                <li className="list-group-item">
                  DERECHO DE ADMISIÓN: S/.875.00 (Ochocientos setenta y cinco
                  con 00/100 soles)
                </li>
              </ul>
            </div>
            <div className=" listas__subconteniedo">
              <ul className="list-group">
                <li className="list-group-item active">Modo de Pago</li>
                <li className="list-group-item">
                  Solicitar orden de pago BCP al correo posgrado_fim@uni.edu.pe,
                  brindando los siguientes datos: Nombres y Apellidos, DNI y
                  número de celular y en el caso de solicitar factura indicar
                  razón social, RUC y dirección
                </li>
              </ul>
            </div>
          </div>
        </Contenido>
      </Modal>
    </>
  );
};

export default ModalExamenAdminsio;

const Contenido = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    font-size: 42px;
    font-weight: 700;
    margin-bottom: 30px;
    margin-top: 30px;
  }

  p {
    font-size: 18px;
    margin-bottom: 20px;
    margin-left: 20px;
  }

  img {
    width: 100%;
    vertical-align: top;
    border-radius: 3px;
  }
`;
